.cozinha{
    display: flex;
    flex-wrap: wrap;
}

.Card-cozinha{
    border-radius: 2rem;
    overflow: hidden;
    text-align: center;
    height: 25%;
    width: 25%;
    margin: 2rem;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.15);
}

.Card-cozinha img{
    border-radius: 2rem;
    width: 100%;
    height: 100%;
}

.Card-cozinha h3{
    color: rgb(0, 0, 0);
    font-size: 1rem;
}