.lista-icon{
    display: flex;
    justify-content: center;
    margin: 2rem 0rem;
}

.icon{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 6.5rem;
    height: 6.5rem;
    text-decoration: none;
    border-radius: 50%;
    background: linear-gradient(35deg, #494949, #313131);
    color: white;
    margin-left: 2rem;
}

.icon h4{
    color: white;
    font-size: .8rem;
}