.Populares{
    margin: 4rem 0rem;
    min-width: 25rem;
    min-height: 20rem;
}

.Card{
    border-radius: 2rem;
    overflow: hidden;
    text-align: center;
    height: 100%;
    width: 100%;
}

.Card img{
    border-radius: 2rem;
    width: 100%;
    height: 100%;
}

h3{
    color: rgb(255, 255, 255);
    font-size: 1rem;
}

.Gradient{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    z-index: 3;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7));
    border-radius: 2rem;
}

h2{
    margin: 2rem 0;
}


